/*----------------------------------------------------------------------------*\
    $UTILITIES-RESPONSIVE
/*----------------------------------------------------------------------------*\
    Contains useful responsive related functions
/*----------------------------------------------------------------------------*/

//------------------------------------------------------------------------------
// IE10 in Windows (Phone) 8
//------------------------------------------------------------------------------
// Support for responsive views via media queries is kind of borked in IE10, for
// Surface/desktop in split view and for Windows Phone 8. This particular fix
// must be accompanied by a snippet of JavaScript to sniff the user agent and
// apply some conditional CSS to *only* the Surface/desktop Windows 8. Look at
// our Getting Started page for more information on this bug.
//
// For more information, see the following:
//
// Issue: https://github.com/twbs/bootstrap/issues/10497
// Docs: http://getbootstrap.com/getting-started/#support-ie10-width
// Source: http://timkadlec.com/2013/01/windows-phone-8-and-device-width/
// Source: http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
//------------------------------------------------------------------------------
@-ms-viewport { width: device-width; }

//------------------------------------------------------------------------------
// Visibility utilities
//------------------------------------------------------------------------------
.visible-xs,
.visible-sm,
.visible-md,
.visible-lg,
.visible-xl { .responsive-invisibility(); }

.visible-xs-block, .visible-xs-inline, .visible-xs-inline-block,
.visible-sm-block, .visible-sm-inline, .visible-sm-inline-block,
.visible-md-block, .visible-md-inline, .visible-md-inline-block,
.visible-lg-block, .visible-lg-inline, .visible-lg-inline-block,
.visible-xl-block, .visible-xl-inline, .visible-xl-inline-block {
    display: none !important;
}

.visible-xs {
    @media (max-width: @screen-xs-max) { .responsive-visibility(); }
}

.visible-xs-block {
    @media (max-width: @screen-xs-max) { display: block !important; }
}

.visible-xs-inline {
    @media (max-width: @screen-xs-max) { display: inline !important; }
}

.visible-xs-inline-block {
    @media (max-width: @screen-xs-max) { display: inline-block !important; }
}

.visible-sm {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
        .responsive-visibility();
    }
}

.visible-sm-block {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
        display: block !important;
    }
}

.visible-sm-inline {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
        display: inline !important;
    }
}

.visible-sm-inline-block {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
        display: inline-block !important;
    }
}

.visible-md {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
        .responsive-visibility();
    }
}

.visible-md-block {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
        display: block !important;
    }
}

.visible-md-inline {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
        display: inline !important;
    }
}

.visible-md-inline-block {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
        display: inline-block !important;
    }
}

.visible-lg {
    @media (min-width: @screen-lg-min) { .responsive-visibility(); }
}

.visible-lg-block {
    @media (min-width: @screen-lg-min) { display: block !important;}
}

.visible-lg-inline {
    @media (min-width: @screen-lg-min) { display: inline !important; }
}

.visible-lg-inline-block {
    @media (min-width: @screen-lg-min) { display: inline-block !important; }
}

.visible-xl {
    @media (min-width: @screen-xl-min) { .responsive-visibility(); }
}

.visible-xl-block {
    @media (min-width: @screen-xl-min) { display: block !important;}
}

.visible-xl-inline {
    @media (min-width: @screen-xl-min) { display: inline !important; }
}

.visible-xl-inline-block {
    @media (min-width: @screen-xl-min) { display: inline-block !important; }
}

.hidden-xs {
    @media (max-width: @screen-xs-max) { .responsive-invisibility(); }
}

.hidden-sm {
    @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
        .responsive-invisibility();
    }
}

.hidden-md {
    @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
        .responsive-invisibility();
    }
}

.hidden-lg {
    @media (min-width: @screen-lg-min) and (max-width: @screen-lg-max) {
        .responsive-invisibility();
    }
}

.hidden-xl {
    @media (min-width: @screen-xl-min) { .responsive-invisibility(); }
}

//------------------------------------------------------------------------------
// Print utilities
//------------------------------------------------------------------------------
// Media queries are placed on the inside to be mixin-friendly.
//------------------------------------------------------------------------------
.visible-print {
  .responsive-invisibility();
    @media print { .responsive-visibility(); }
}

.visible-print-block {
    display: none !important;
    @media print { display: block !important; }
}

.visible-print-inline {
  display: none !important;
    @media print { display: inline !important; }
}

.visible-print-inline-block {
  display: none !important;
    @media print { display: inline-block !important; }
}

.hidden-print {
    @media print { .responsive-invisibility(); }
}
