//------------------------------------------------------------------------------
// Grid system
//------------------------------------------------------------------------------
// Generate semantic grid columns with these mixins.
//------------------------------------------------------------------------------

//------------------------------------------------------------------------------
// Centered container element
//------------------------------------------------------------------------------
.container-fixed(@gutter: @grid-gutter-size) {
    margin-right: auto;
    margin-left: auto;
    padding-left:  (@gutter / 2);
    padding-right: (@gutter / 2);

    &:extend(.clearfix all);
}

//------------------------------------------------------------------------------
// Creates a wrapper for a series of columns
//------------------------------------------------------------------------------
.make-row(@gutter: @grid-gutter-size) {
    margin-left:  (@gutter / -2);
    margin-right: (@gutter / -2);

    &:extend(.clearfix all);
}

//------------------------------------------------------------------------------
// Generate the extra small columns
//------------------------------------------------------------------------------
.make-xs-column(@columns; @gutter: @grid-gutter-size) {
    float: left;
    min-height: 1px;
    width: percentage((@columns / @grid-columns));
    padding-left:  (@gutter / 2);
    padding-right: (@gutter / 2);
    position: relative;
}
.make-xs-column-offset(@columns) { margin-left: percentage((@columns / @grid-columns)); }
.make-xs-column-push(@columns) { left: percentage((@columns / @grid-columns)); }
.make-xs-column-pull(@columns) { right: percentage((@columns / @grid-columns)); }

//------------------------------------------------------------------------------
// Generate the small columns
//------------------------------------------------------------------------------
.make-sm-column(@columns; @gutter: @grid-gutter-size) {
    min-height: 1px;
    padding-left:  (@gutter / 2);
    padding-right: (@gutter / 2);
    position: relative;

    @media (min-width: @screen-sm-min) {
        float: left;
        width: percentage((@columns / @grid-columns));
    }
}

.make-sm-column-offset(@columns) {
     @media (min-width: @screen-sm-min) { margin-left: percentage((@columns / @grid-columns)); }
}

.make-sm-column-push(@columns) {
     @media (min-width: @screen-sm-min) { left: percentage((@columns / @grid-columns)); }
}

.make-sm-column-pull(@columns) {
    @media (min-width: @screen-sm-min) { right: percentage((@columns / @grid-columns)); }
}

